//Primary Text colors
$very-dark-desaturated-blue: hsl(238, 29%, 16%);
$soft-red: #e69c33;

// Background gradient:
$soft-violet: hsl(273, 75%, 66%);
$soft-blue: hsl(240, 73%, 65%);

//Neutral Text colors
$very-dark-grayish-blue: hsl(237, 12%, 33%);
$dark-grayish-blue: hsl(240, 6%, 50%);

//Dividers
$light-grayish-blue: hsl(240, 5%, 91%);
button:focus:not(:focus-visible) {
  outline: 0;
}
.faq {
  width: 100%;
  // padding-top: 65px;
  // padding-bottom: 80px;

  &__title {
    margin: 0;
    padding-bottom: 25px;
  }

  &__question {
    max-width: 350px;
    width: 100%;
    margin-top: 10px;
    // border-bottom: 1px solid $light-grayish-blue;
  }

  &__question-button {
    // width: 100%;
    display: flex;
    justify-content: space-between;
    border: none;
    background-color: transparent;
    padding: 0px;
    cursor: pointer;
    // padding-top: 18px;
    // padding-bottom: 18px;
    text-align: left;
    color: #fff;
    &::after {
      content: url(../../../assets/images/Dropdown.png);
      margin-right: 17px;
      transform: rotate(-90deg);
    }

    &:hover {
      color: $soft-red;
    }
  }

  &__desc {
    // margin-left: -40px;
    color: #fff;
    padding-right: 17px;
    margin-top: 0;
    margin-bottom: 0;
    height: 0;
    overflow: hidden;
    transition: all 200ms ease-out;
  }
}
.faq__desc.show-description {
  height: auto;
  margin-bottom: 15px;
}

/* Arrows */

/* closed */
.faq__question-button::after {
  transition: transform 300ms ease-out;
}

/* open */
.font-weight-bold::after {
  transform: rotate(0deg);
}
