.title-all {
  font-family: Helvetica Neue;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  color: #ffffff;
  border-bottom: 1px solid #d3d3d3;
  padding-bottom: 8px;
  display: table-cell;
  margin-bottom: 2px;
}
.title-type {
  margin-top: 18px;
  font-family: Helvetica Neue;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.title-product {
  font-family: Helvetica Neue;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  margin-top: 8px;
  text-align: center;
  margin-bottom: 48px;
}
.img-product {
  width: 100%;
  object-fit: cover;
  height: 253px;
}
.produtct-name {
  margin-top: 64px;
  font-family: Helvetica Neue;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.view-properties {
  margin-right: 22px;
  margin-left: 22px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.title-properties {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  align-items: center;
  color: #868686;
}
.title-price {
  margin-top: 24px;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  display: flex;
  align-items: center;
  color: #868686;
}
.detail-properties {
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  align-items: center;
  color: #ffffff;
  margin-top: 8px;
}
.content-properties {
  margin-top: 24px;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 32px;
  align-items: center;
  text-align: center;
  color: #ffffff;
}
.slider-inactive {
  transform: scaleX(0.89);
  transition: ease-in-out 0.3s;
  opacity: 0.5;
}
.slider-active {
  transform: scaleX(1.11);
  transition: ease-in-out 0.5s;
}
.text-similar {
  margin-top: 80px;
  margin-bottom: 24px;
  font-family: Helvetica Neue;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
