.swiper-partner-view {
  margin-top: 146px;
  height: 435px;
  background-color: #313131;
  padding-left: 10%;
  padding-right: 10%;
}
.swiper-partner-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.partner-img {
  width: 162px;
  height: 162px;
  object-fit: cover;
  border-radius: 80px;
}
.mySwiper {
  top: -81px !important;
}
.partner-title {
  font-family: Lexend;
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
  /* or 150% */

  text-align: center;
  letter-spacing: -0.02em;
  color: #ffffff;
  margin-top: 56px;
}

.partner-footer {
  font-family: Lexend;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */

  text-align: center;
  letter-spacing: -0.02em;

  /* Neutral/White */

  color: #ffffff;
  margin-top: 56px;

  opacity: 0.8;
}
@media (max-width: 1024px) {
  .partner-title {
    font-size: 18px;
    line-height: 30px;
  }
  .partner-footer {
    font-size: 16px;
    line-height: 24px;
  }
}
